<template>
  <div class="newUser" id="Guarantee">
     <!-- 银行卡部分 -->
      <div class="bankCard bgfff icon pl12 bb pr jt" @click="showBank">   
        <div class="dis-inline ver-mid">
          <img :src='this.contextPath+"/static/images/bank-logo/"+bankCardInfo.bankCode+".png"' @error="imgFail($event)" width="16" height="16" alt="banklogo">
        </div>
        <div class="dis-inline ver-mid">
          <span class="fs16 c333">{{bankCardInfo.bankName}} </span>
        </div>
      </div>

      <div class="bankCard bgfff icon pl12 bb pr " :class="{jt:this.periodList[this.bankCardInfoTotal[0].bankCode].length > 1}"  @click="showStage">
        <div class="dis-inline fs16 stagetext">
          <span class="fs16 c666">分期期数</span>
        </div>
        <span class="stageNo fs16 c666">{{this.period}}</span>期
      </div>
      <!-- 钱 -->
      <ul class="info bgfff pl12 c666 pt10 pb10">
          <li class="li">
            <span class="text">订单金额:</span>&nbsp;<i class="ver-t">￥</i><i class="orderMoney">{{this.orderAmount}}</i>
          </li>
           <li class="li">
            <span class="text">手续费率:</span>&nbsp;<i class="ver-t"></i><i class="handleMoney">{{this.serviceChargeRate}}%</i>
          </li>
          <li class="li">
            <span class="text">手续费:</span>&nbsp;<i class="ver-t">￥</i><i class="handleMoney">{{this.serviceCharge}}</i>
          </li>
           <li class="li">
              <span class="text">每期应还:</span>&nbsp;<i class="ver-t">￥</i><i class="firstMoney">{{this.amountPerPeriod}}</i>
          </li>
          <li class="fs12 cred mt10">实际扣款金额以银行账单为准</li>
      </ul>
      <!-- 输入卡号 -->
      <p class="fs12 c999 mt20 pl12">请使用本人银行卡进行操作</p>
       <form class="mt10 bgfff" id="form" @submit.prevent>
        <label>
          <div class="li pr pl12 pr12 w borderbox fs16 ">
              <input class="required  cardNo input-text " name="cardno" _name="cardno" type="tel" placeholder="请输入银行卡号" v-model="cardNo">
              <i class="del pa icon" style="top:10px;" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
      </form>
      <div class="mt20 pl12">
        <label for="agreement">
            <input class="hide" id="agreement" type="checkbox" :checked="protocal" v-model="protocal">
            <i class="icon agreement"></i>已阅读并同意
        </label><a class="c2eb944 pr protocal" @click="showProtocal" >《服务协议》</a>
      </div>
      <div class="pl12 pr12 mt30">
        <button class="w" id="submit" type="button" :disabled="!protocal" @click="sureOrder">
          <i>下一步</i>
        </button>
      </div>
        <!-- 银行卡弹框 -->
      <div class="bank-tip" v-show="isShowBankTip">
        <h2>更换银行</h2>
        <div class="bt pt10 pb10 li" v-for="(item,index) of this.bankCardInfoTotal" :class='{bgf5f5f5:item.status=="unusable"}' @click="changeBank(item)">
          <div class="dis-inline ver-mid pl12">
            <img  :src='this.contextPath+"/static/images/bank-logo/"+item.bankCode+".png"' width="30" height="30" alt="banklogo">
            <!-- <img  :src='"../../static/images/bank-logo/"+item.bankCode+".png"' width="30" height="30" alt="banklogo"> -->
          </div>
          <div class="dis-inline ver-mid ml6">
            <p class="fs14 c333">{{item.bankName}}</p>
            <span class="fs12 c999"><i class="ml10" v-if='item.cardType == "CREDIT"'>信用卡</i></span>
          </div>
        </div>
        
      </div>
      <!-- 支持期数 -->
      <div class="bank-stage"  v-show="isShowperiod">
        <h2 >更换期数</h2>
        <div class="c333 tc fs16" >
          <p class="sno bt" @click="changeStage(no)" v-for="no of  this.periodList[this.bankCardInfo.bankCode]">{{no}}期</p>
        </div>
      </div>
      <!-- 支持期数 结束-->
      <div id="areaData">
        <!-- 这是存放通联返回的表单数据，为了表单同步提交 -->
      </div>
     <!-- 服务协议 -->
        <Protocal :isProtocal="isProtocal"></Protocal>
      <!-- 服务协议 -->
       <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
  </div>
</template>

<script>
let ERR_OK = 'success'; //返回成功码
import AddItem from '@/views/addItem.vue' 
import Protocal from '@/components/protocal.vue'
export default {
  name: 'Guarantee',
  data () {
    return {
      contextPath:contextPath,
      protocal: true,//同意协议
      isProtocal:false, //服务协议显示
      isShowMask:false ,//mask遮罩
      bankCardInfo:[],//bankCardInfoTotal first
      period:[],//period first
      bankCode:'',  //first 银行编码 
      bankCardInfoTotal:usableBankList, //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardType":"信用卡","number":"2218"}]
      orderAmount:'',  //订单金额
      serviceCharge:'',  //手续费
      serviceChargeRate:'',  //手续费率
      amountPerPeriod:'',  //每期还款额
      cardNo:'',  //卡号
      isShowBankTip:false ,//签约卡列表遮罩
      isShowperiod:false ,//支持期数 
      periodList:periodList ,//
      money:{}  //缓存已有卡信息账户
    }
  },
  created() {
    var self = this
    this.$bus.$on("protocal",function(msg){ //bus监听显示和隐藏协议
      self.isProtocal = msg
      self.isShowMask = msg
    })
    this.$bus.$on("agree",function(msg){ //bus监听显示和隐藏协议
      self.protocal = msg
    })
    this.bankCardInfo = this.bankCardInfoTotal[0] //first 展示银行信息
    this.period = this.periodList[this.bankCardInfoTotal[0].bankCode][0]// first 展示期数
    this.bankCode = this.bankCardInfo.bankCode  //first 展示银行编码
    this.getMoneyInfo(token,this.bankCode,this.period)
  },
   mounted() { 
    if(document.querySelector("#form")){
      User.form.validate("#form");
    }
  },
  methods:{
    imgFail(e){ //图片加载失败
      e.target.src = ''
      return false
    },
    getMoneyInfo(token,period,bankCode) {  //各种费率金额信息
      var totalMoney = JSON.parse(localStorage.getItem(token))
      if(totalMoney && totalMoney[period+bankCode] ){
        this.getMoneyStorage(totalMoney,token,period,bankCode)
        return false
      }
      var param = {}
        param.token = token
        param.bankCode = this.bankCode
        param.period = this.period
      this.$http.get(Request.url+'/guarantee/ajax/prepay',{params:param}).then(res => {
        var data = typeof res.body == 'string' ? JSON.parse(res.body) : res.body
        if(data.bizStatus === ERR_OK){
          this.$store.state.bankCode = data.bankCode;   //data.bankCode
          this.$store.state.period = data.period;   //data.bankCode
          this.$store.state.preRouteId = data.preRouteId;   //data.bankCode
          this.orderAmount = data.orderAmount
          this.serviceCharge = data.serviceCharge
          this.serviceChargeRate = data.serviceChargeRate
          this.amountPerPeriod = data.amountPerPeriod
          this.$store.state.needSupply = data.needSupply
          this.$store.state.cardItemNecessary = data.cardItemNecessary
          this.$store.commit("cardInfo");  //触发其他组件更新数据
          this.setMoneyStorage(data,token,period,bankCode)
        }else{
          this.errorProcess('手续费读取失败，请刷新页面或更换银行、期数')
          this.clearFate() //清除费率信息
          this.clearCardNo()   //清除卡號
        }
      })
    },
    sureOrder(){ //下一步
      var result = User.form.autoValidate("#form");
      var self = this
      if(result){
        this.$store.state.bankName = this.bankCardInfo.bankName
        this.$store.state.cardNo = this.cardNo
        this.$store.commit("cardInfo");  //触发其他组件更新数据
        var currentCard = {}
        currentCard.bankName = this.$store.state.bankName
        currentCard.cardNo = this.$store.state.cardNo
        localStorage.setItem("currentCard",JSON.stringify(currentCard))
        if(this.$store.state.needSupply == 'Y'){  //补充项不为空
          this.getCardType()  //卡bin校验
        }else{
          this.makeOrder()//直接分期支付
        }
      }
    },
    getCardType(){  //卡bin  && 拿补充项中得卡类型
      var self = this
      this.$http.get(Request.url+'/guarantee/ajax/check',{params: {'token':token,'bankCode':self.$store.state.bankCode,'cardNo':BASE64.encoder(self.cardNo)}}).then((res) => {  //单独接口，只为了拿卡类型，信用卡
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body
        if(data.bizStatus == 'success'){
          this.$router.push({name:'AddItem',path:'/AddItem',component: AddItem,params:{'token':token,'bankCode':self.$store.state.bankCode,"period":self.$store.state.period,'preRouteId':self.$store.state.preRouteId}})
        }else{
          this.errorProcess(data.errormsg,data.errorcode) //失败
        }
      })
    },
    makeOrder(){   //这个直接分期支付
      var self = this
      var param = {}
      param.token = token
      param.bankCode = this.$store.state.bankCode   //入参全从store对象里取
      param.period = this.$store.state.period
      param.preRouteId = this.$store.state.preRouteId
      param.cardNo = BASE64.encoder(this.cardNo)
      this.$http.get(Request.url+'/guarantee/ajax/pay',{params:param}).then(res => {
        var data = typeof res.body == 'string' ? JSON.parse(res.body) : res.body
        if(data.bizStatus == ERR_OK){
          this.setForm(data.urlInfo)
        }else if(data.bizStatus == 'orderFailed'){  //失败
          this.errorProcess(data.errormsg,data.errorcode) 
        }else if(data.bizStatus == 'failed'){
          location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay=true");
        }
      })
    },
    setMoneyStorage(data,token,period,bankCode){ //设置缓存
      this.money[period+bankCode] = {}
      this.money[period+bankCode].orderAmount = data.orderAmount
      this.money[period+bankCode].serviceCharge = data.serviceCharge
      this.money[period+bankCode].serviceChargeRate = data.serviceChargeRate
      this.money[period+bankCode].amountPerPeriod = data.amountPerPeriod
      this.money[period+bankCode].bankCode = data.bankCode
      this.money[period+bankCode].period = data.period
      this.money[period+bankCode].preRouteId = data.preRouteId
      this.money[period+bankCode].needSupply= data.needSupply
      this.money[period+bankCode].cardItemNecessary= data.cardItemNecessary
      localStorage.setItem(token,JSON.stringify(this.money))
    },
    getMoneyStorage(totalMoney,token,period,bankCode){  //取缓存
      var data = totalMoney[period+bankCode]
      this.orderAmount= data.orderAmount
      this.serviceCharge= data.serviceCharge
      this.serviceChargeRate= data.serviceChargeRate
      this.amountPerPeriod= data.amountPerPeriod
      this.$store.state.bankCode = data.bankCode
      this.$store.state.period = data.period
      this.$store.state.preRouteId = data.preRouteId
      this.$store.state.needSupply = data.needSupply
      // this.cardItemNecessary = data.cardItemNecessary
      return false
    },
    clearFate(){
      // 各种费率信息清空
      this.orderAmount = ''
      this.serviceChargeRate = ''
      this.serviceCharge = ''
      this.amountPerPeriod = ''
    },
    clearCardNo(){
      // 卡号清空
      this.cardNo = ''
    }
  },
 components:{
    Protocal
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.cff491f{ color:#ff491f;} input{-webkit-tap-highlight-color: rbga(0,0,0,0);}
.borderbox{box-sizing: border-box;}
.newUser{
  .protocal{z-index:9;}
  #submit[disabled]{
    background: #ccc;
  }
  #agreement:checked +.icon.agreement{  //协议
    &:before{
      display: inline-block;
      width:18.4px;
      height:18.4px;
      line-height: 18.4px;
      content:"\e7c0";
      font-size:16px;
      color:#2eb944;
      vertical-align: top;
    }
  }
  #agreement +.icon.agreement{  //协议
    &:before{
      display: inline-block;
      width:18.4px;
      height:18.4px;
      line-height: 18.4px;
      content:"\e7c1";
      font-size:16px;
      color:#ccc;
      vertical-align: top;
    }
  }
  .cardNo{height:32px;line-height: 32px;padding:6px;-webkit-tap-highlight-color: rgba(0, 0, 0, 0);}
  .bankCard{//  银行卡部分
      height:50px;
      line-height:50px;
      .cardtype{  //信用卡
        margin-right: 40px;
        color:#d8d8d8;
      }
      .stagetext{  //分期期数
        width:80px;
        font-weight:normal
      }
      &:active{
        background: #f5f5f5;
      }
    }
    
  .info{   //<!-- 钱 -->
      .orderMoney  ,.handleMoney ,.firstMoney{
        display:inline-block;
        vertical-align: top;
      }
      .li{
         height:36px; 
         line-height: 36px;
         .text{
          display:inline-block;
          width:110px;
          height:36px; 
          text-align: justify;
          overflow: hidden;
          &:after{
            content:'';
            width:100%;
            display: inline-block;
             overflow: hidden;
          }
        }
      }
    }
  
  .bank-tip{  //<!-- 银行卡弹框 -->
    .ml6{margin-left: 6px;}
    .li:active{background: #f5f5f5;}
      width:88%;
      max-height:80%;
      overflow-y: auto;
      background: #fff;
      position: fixed;
      top:50px;
      left:50%;
      margin-left:-44%;
      border-radius:4px;
      transition:opacity 0.5s;
      z-index:11;
      -webkit-overflow-scrolling:touch;
      h2{
        padding:10px 13px;
        height:20px;
        font-weight: 700;
        font-size: 16px;
      }
      .add{
        padding:14px 12px;
        a{
          color:#666;
        }
      }
      .addSymbol{
        width:30px;height:30px;
        text-align: center;
        display: inline-block;
      }
    }
  .bank-stage{  //<!-- 支持期数 -->
    .li:active{
      background: #f5f5f5;
    }
    width:88%;
    max-height:80%;
    overflow-y: auto;
    background: #fff;
    position: fixed;
    top:50px;
    left:50%;
    margin-left:-44%;
    border-radius:4px;
    transition:opacity 0.5s;
    z-index:11;
    -webkit-overflow-scrolling:touch;
    h2{
      padding:10px 13px;
      height:20px;
      font-weight: 700;
      font-size: 16px;
    }
    .sno{
      line-height: 40px;
      height: 40px;
      width:100%;

      &:active{
        background: #f5f5f5;
      }
    }
  }
  .jt:after{  //有 > jt
      position: absolute;
      right:12px;
      line-height: 46px;
      content:"\e65e";
      font-size:22px;
      color:#d8d8d8;
    }
}
</style>



