// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'

import router from './router'
import App from './App'
import BASE64 from '../static/guarantee-stage-static/js/jbase64.min.js'
import Util from '../static/guarantee-stage-static/js/util.js'
import User from '../static/guarantee-stage-static/js/user.js'
import Tip from '../static/guarantee-stage-static/js/tip.js'
import Common from '../src/util/util.js'
 
import Request from '../static/guarantee-stage-static/js/request.js'
import'../static/guarantee-stage-static/css/h5.css'
import'../static/guarantee-stage-static/css/tip.less'
Vue.use(Common)
Vue.use(Vuex)

const vuex_store = new Vuex.Store({
	state:{
		cardItemNecessary:null, //补充项
		"needSupply":"N",  //需要补充项标识
		"bankName":"",  //编码
		"bankCode":"",  //编码
		"period":"",   //请求期数
		"preRouteId":"", //预路由id
		"cardNo":""   //输入嘚卡号
	},
	mutations:{
		cardInfo(state){
			state.bankName
			state.bankCode
			state.period
			state.cardNo
			state.cardItemNecessary
		}
	}
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store:vuex_store,  //vuex对象挂载到vue对象上
  template: '<App/>',
  components: { App }
})



