export default{
	install:function(Vue,opt){
		Vue.mixin({
			methods:{
				showProtocal() {
			      // 服务协议 
			      this.isShowMask = true;
			      this.isProtocal = true;
			    },
			    closeProtocal() {  // 服务协议 
			      this.isShowMask = false;
			      this.isProtocal = false;
			      this.protocal = true;
			    },
				showBank:function() {  //显示银行
				    this.isShowMask = true;
				    this.isShowBankTip = true;
			    },
			    showStage:function() {  //显示期数
			        this.isShowMask = true;
	      			this.isShowperiod = true;
			    },
			    mask:function(){ //遮罩点击
			    	this.isProtocal = false; // 服务协议 
			    	this.isShowMask = false;
			    	this.isShowperiod = false;
			    	this.isShowBankTip = false;
			    	this.hideTip()
			    },
			    hideTip(){  //错误弹框提示
					var tip1 = dom.querySelector("#tip1")
			    	if(tip1){
			    		tip1.classList.add("none");
			    	}
			    },
			    changeStage: function(no){  //更改期数
			      this.isShowMask = false
			      this.isShowperiod = false
			      this.period = no
				  this.bankCode = this.bankCardInfo.bankCode
			      this.getMoneyInfo(token,this.bankCode,this.period)
			    },
			    changeBank :function(item) {  //更改银行
			      	this.period = this.periodList[item.bankCode][0]   //分期数目维护
			      	this.bankCode = item.bankCode
			        this.isShowMask = false;
			        this.isShowBankTip = false;
			        this.bankCardInfo = item;
			        this.getMoneyInfo(token,this.bankCode,this.period)
			    },
			    clearFate(){
			      // 各种费率信息清空
			      this.orderAmount = ''
			      this.serviceChargeRate = ''
			      this.serviceCharge = ''
			      this.amountPerPeriod = ''
			    },
			    clearCardNo(){
			      // 卡号清空
			      this.cardNo = ''
			    },
			    formatCheck(item) { //校验处理
		    		var form = dom.querySelector('#form');
		    		User.form.hideErrorMessage(form);  //得到焦点隐藏错误信息
		    		if(item.srcElement.name == 'idNo'){
		    			item.srcElement.type = 'text';
		    		}
			    	var result = User.form.doValidate(item.srcElement);
                    if (!result) {
                        return false;
                    }
			    },
			    errorProcess(msg,code){ //请求失败，错误提示，统一处理
			    	var self = this
			    	this.isShowMask = true
			    	var text = code ? msg+'('+code+')' : msg
			        Tip.titleContentBtn('提示',text,function(){
			            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
			              self.isShowMask = false;
			              dom.querySelector("#tip1").classList.add("none")
			            },false);
			        })
			    },
			    setForm(obj) {   //创建表单提交数据到银联
			      var charset = obj.charset
			      var method = obj.method
			      var params = obj.params
			      var url = obj.url
			      dom.querySelector("#areaData").innerHTML = '';
			      var html = '';
			      html += '<form name="form2" id="form2" action="'+url+'" method="'+method+'"  accept-charset="'+charset+'">'
			     for(var item in params){
			        html += '<input type="hidden" name="'+item+'" value="'+params[item]+'" />'
			     }
			     html += '<form>';
			     dom.querySelector("#areaData").insertAdjacentHTML('beforeEnd',html)
			     dom.querySelector("#form2").submit();
			    }
			}
		});
		Vue.prototype.delInfo=function(e){  //删除输入框内容
		  	var obj = e.target;
		  	obj.parentNode.querySelector(".input-text").value=''; 
		}
	}	
}



