import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import vueEventBus from 'vue-event-bus'
import Guarantee from '@/views/guarantee'  //新用户
import AddItem from '@/views/addItem'  //补充项
 
Vue.use(Router)
Vue.use(Resource)
Vue.use(vueEventBus)
const router = new Router({
  routes: [
    {
      path: '/',  //新用户
      name: 'Guarantee',
      component: Guarantee
    },
    {
      path: '/AddItem/:token/:bankCode/:period/:preRouteId',  //补充项
      name: 'AddItem',
      component: AddItem
    }
    
  ]
})
export default router;




