<template>
	<div id="addItem">
    <p class="c999 fs12 pl12 pt10">请填写银行卡信息</p>
		<div class="content mt10">
        <!-- 银行卡部分 -->
        <div class="bankCard bgfff icon pl12 bt bb pr">   <!-- 点击显示银行弹框 -->
          <div class="dis-inline ver-t">
            <img  :src='this.contextPath+"/static/images/bank-logo/"+currentCard.bankCode+".png"' width="16" height="16" alt="banklogo">
            <!-- <img class="mt4" :src='"../../static/images/bank-logo/"+currentCard.bankCode+".png"' width="18" height="18" alt="banklogo"> -->
          </div>
          <div class="dis-inline">
            <p class="fs16 c666 mb10">{{currentCard.bankName}}</p>
            <i class="fs16 c666 mr10">尾号{{currentCard.cardLater4}}</i>
            <i class="fs14 c999 mt10">{{cardType}}</i>
          </div>
          <router-link tag="span" class="change" :to="{name: 'Guarantee'}">更换</router-link>
        </div>
        <!-- 商户基本信息 -->
        <p class="c999 fs12 pl12 pt10 pb10 bold">请使用本人信息进行支付，支付成功后不可更改</p>
        <form class="bgfff fs18" name="form" id="form">
          <label v-if="cardItemNecessary.needOwner">
            <div class="info li pr " :class='{"bgEBEBE4":cardInfo.owner}'>
              <mark class="dis-inline text c333 pl12">姓名</mark>
              <input v-if="cardInfo.owner" class=" input-text" name="owner" _name="chinese" encrypt="true" type="text" :value="cardInfo.owner" :disabled="true">
              <input v-else class="required input-text" name="owner" _name="chinese" encrypt="true" type="text" placeholder="您的真实姓名" @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>          
          <label v-if="cardItemNecessary.needIdNo">
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.idNo}'>
                <mark class="dis-inline text c333 pl12">身份证号码</mark>
                <input v-if="cardInfo.idNo" class=" input-text" name="idNo" encrypt="true" type="number" :value="cardInfo.idNo" :disabled="true">
                <input v-else class="required input-text" name="idNo" _name="idno" maxlength="18" encrypt="true" type="number" placeholder="真实证件号" @blur.prevent="formatCheck($event)">
                <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <div>
            <label v-if="cardItemNecessary.needCvv">
              <div class="info li bt pr" :class='{"bgEBEBE4":cardInfo.cvv}'>
                <mark class="dis-inline text c333 pl12">C V V</mark>
                <input v-if="cardInfo.cvv" class=" input-text" name="cvv"  encrypt="true" type="tel" :value="cardInfo.cvv" :disabled="true">
                <input v-else class="required input-text" name="cvv" _name="threeOrFour" maxlength="4" encrypt="true" type="tel" placeholder="卡背面后三位数字" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
            <label v-if="cardItemNecessary.needAvlidDate">
              <div class="info bt li pr" :class='{"bgEBEBE4":cardInfo.avlidDate}'>
                  <mark class="dis-inline text c333 pl12">有效期</mark>
                  <input v-if="cardInfo.avlidDate" class=" input-text" name="avlidDate"  encrypt="true" type="tel" :value="cardInfo.avlidDate" :disabled="true">
                  <input v-else class="required input-text" name="avlidDate" _name="fourNo" maxlength="4" encrypt="true" type="tel" placeholder="月/年(如03/18 输入0318)" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
          </div>
          <label v-if="cardItemNecessary.needPhoneNo" >
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.phoneNo}'>
              <mark class="dis-inline text c333 pl12">手机号</mark>
              <input v-if="cardInfo.phoneNo" class=" input-text" name="phoneNo" encrypt="true" type="tel" :value="cardInfo.phoneNo" :disabled="true">
              <input v-else class="required input-text" name="phoneNo" _name="phone" maxlength="11" encrypt="true" type="tel" placeholder="银行预留手机号" @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
        </form>
        <div class="pl12 pr12 mt30">
          <button class="w" :class='{"bgccc":resubmit}' id="submit" type="button" @click="stagePay" :disabled="resubmit">确认支付</button>
        </div>
      </div>
      <div id="areaData">
        <!-- 这是存放通联返回的表单数据，为了表单同步提交 -->
      </div>
     <!-- 遮罩层 开始 -->
    <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
    <!-- 遮罩层结束 -->
	</div>
</template>
<script>
let ERR_OK = 'success'; //返回成功码
export default {
  name: 'AddItem',
  data () {
     return {
      resubmit:false,//防重提交
      cardType:'', //卡类型
      isShowMask: false,  //遮罩
     	cardItemNecessary:{},//卡必填项
      cardInfo:{
        "owner":"",
        "idNo":"",
        "cvv":"",
        "avlidDate":"",
        "phoneNo":""
      },//卡信息
      currentCard:{
        "bankName":"",
        "bankCode":"",
        "cardLater4":""
      },
      contextPath:contextPath //路径
     }
  },
 
  created() {
    var self = this
    if(this.$store.state.cardItemNecessary){
      this.cardItemNecessary = this.$store.state.cardItemNecessary
      this.currentCard.bankName = this.$store.state.bankName
      this.currentCard.bankCode = this.$store.state.bankCode
      this.currentCard.cardLater4 = this.$store.state.cardNo.substring(this.$store.state.cardNo.length-4)
    }else{ 
      var currentCardContent = localStorage.getItem("currentCard") 
      if(currentCardContent){
        this.currentCard.bankName = JSON.parse(currentCardContent).bankName
        this.currentCard.bankCode = this.$route.params.bankCode
        this.currentCard.cardLater4 = JSON.parse(currentCardContent).cardNo.substring(JSON.parse(currentCardContent).cardNo.length-4)
      }else{
        this.errorProcess('亲，不要清理缓存呦~，请返回上一页')
      }
      this.$http.get(Request.url+'/guarantee/ajax/prepay',{params: self.$route.params}).then((res) => {
        var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
          if(data.bizStatus == 'success'){
            this.cardItemNecessary = data.cardItemNecessary
            this.currentCard.bankCode = data.bankCode
          }
      })
    }
    this.getCardType()
  },
  methods:{
    stagePay(item){ //担保分期确认支付
      var self = this
      var result = User.form.autoValidate("#form") //页面元素格式正确才可以发送数据请求
      if (result) {
        var currentCard = localStorage.getItem("currentCard")
        var param = User.form.serialize("#form",self.$route.params)
          param.cardNo = BASE64.encoder(currentCard ? JSON.parse(currentCard).cardNo : self.$store.state.cardNo)
          this.resubmit = true
        this.$http.get(Request.url+'/guarantee/ajax/pay',{params:param}).then((res) => {
          var data = typeof res.body == 'string' ? JSON.parse(res.body) : res.body
          this.resubmit = false
          if(data.bizStatus == ERR_OK){
            this.setForm(data.urlInfo)
          }else if(data.bizStatus == 'orderFailed'){  //失败
            this.errorProcess(data.errormsg,data.errorcode) 
          }else if(data.bizStatus == 'failed'){//失败
            location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay=true")
          }
        })
      }    
    },
    getCardType(){
      var self = this
      var cardno = this.$store.state.cardNo || (localStorage.getItem("currentCard") && JSON.parse(localStorage.getItem("currentCard")).cardNo)
      this.$http.get(Request.url+'/guarantee/ajax/check',{params: {'token':token,'bankCode':self.currentCard.bankCode,'cardNo':BASE64.encoder(cardno)}}).then((res) => {  //单独接口，只为了拿卡类型，信用卡
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body
        if(data.bizStatus == 'success'){
          this.cardType = data.cardType
        }
      })
    }
  } 
}
</script>
<style scoped lang="less">
.mt4{margin-top: 4px;}
.bankCard{
  height:60px;
  padding-top: 14px;
  .change{
    position: absolute;
    right:12px;
    top:36%;
    width:54px;
    height:18px;
    line-height:18px;
    font-size: 14px;
    color:#2fbf45;
    text-align: right;
    background: url("../../static/guarantee-stage-static/images/change-icon.png") no-repeat left ;
    background-size: 20px 16px;
  }
}
.bgEBEBE4{background-color:#EBEBE4;}
.foot{width:60%;position: fixed;bottom:30px;left:50%;margin-left:-30%}
 #form{
    label{
      display: block;
      height:44px;
      mark{
       display: inline-block;
        width: 73px;
        height: 100%;
        line-height: 44px;
        background-color: transparent;
        text-align: justify;
        overflow: hidden;
      }
      mark:after {
          content: " ";
          display: inline-block;
          width: 100%
      }
    }
    .info{
    width:100%;
    height:44px;
    line-height: 44px;
    .text{
      width:72px;
      margin-right: 14px;
    }
    .input-text{
      width:calc(100%-38px);
      height: 40px;
      vertical-align: top;
      &.verifyCode{height: 42px;}
    }
    .resendMessage{
      background: #f0f0f0;
      color:#666;
      height:34px;
      line-height: 34px;
      top:4px;
      right:12px;
    }
    .getCode{
      width:92px;
      height:34px;
      line-height: 34px;
      background: #2eb944;
      color:#fff;
      top:4px;
      right:12px;
      &:active{
        background: #2ea009;
      }
    }
  }
  }
  

</style>


